.home
{
    max-width: 1500px;
    margin: 0 auto 0 auto;
    background-color: rgb(234, 237, 237);
}

.home__row
{
    display: flex;
    width: 100%;
}

.home__image
{
    width: 100%;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
    margin-bottom: -150px;
}